.loginPage {
    position: relative;
    height: 100%;
    background: url("https://assets.nflxext.com/ffe/siteui/vlv3/f841d4c7-10e1-40af-bcae-07a3f8dc141a/684806ba-f8d5-4005-a73c-3c30975fb763/PH-en-20220502-popsignuptwoweeks-perspective_alpha_website_large.jpg") center no-repeat;
    background-size: cover;
}

.loginPage_gradient {
    width: 100%;
    z-index: 1;
    height: 100vh;
    background: rgba(0, 0, 0, 0.4);
    background-image: linear-gradient(to top, rgba(0, 0, 0, 0.8) 0, rgba(0, 0, 0, 0) 60%, rgba(0, 0, 0, 0.8) 100%);
}

.loginPage_logo {
    position: absolute;
    left: 0;
    width: 150px;
    object-fit: contain;
    padding-left: 20px;
}

.loginPage_body {
    position: absolute;
    top: 20%;
    left: 0;
    right: 0;
    margin-left: auto;
    margin-right: auto;
    text-align: center;
    color: white;
    padding: 20px;
    font-size: 1.5rem;
    z-index: 1;
}

.signInPage {
    max-width: 300px;
    padding: 70px;
    margin-left: auto;
    margin-right: auto;
    background-color: rgba(0, 0, 0, 0.75);
    border-radius: 5px;
}

.signInPage > form {
    display: grid;
    flex-direction: column;
}

.signInPage > form > h1 {
    text-align: left;
    margin-bottom: 25px;
}

.signInPage > form > input {
    outline-width: 0;
    height: 40px;
    margin-bottom: 14px;
    border-radius: 5px;
    border: none;
    padding: 5px 15px;
    background-color: #333333;
    color: #8b8988;
    font-size: 16px;
}

.signInPage > form > button {
    padding: 16px 20px;
    font-size: 1rem;
    color: white;
    border-radius: 5px;
    background-color: #e50914;
    font-weight: 600;
    border: none;
    cursor: pointer;
    margin-top: 20px;
}

.signInPage > form > h6 {
    text-align: left;
    margin-top: 20px;
    margin-bottom: 20px;
    font-weight: 400;
    color: #8b8988;
}
.signUpPage_link {
    color: white;
}

.signUpPage_link:hover {
    cursor: pointer;
    text-decoration: underline;
}