.row {
    color: white;
    margin-left: 20px;
}

.row_posters {
    display: flex;
    overflow-y: hidden;
    overflow-x: scroll;
    padding: 20px;
}

.row_posters::-webkit-scrollbar {
    display: none;
}

.row_poster {
    max-height: 180px;
    object-fit: contain;
    margin-right: 20px;
    width: 100%;
    transition: transform 450ms;
    cursor: pointer;
}

.row_poster:hover {
    transform: scale(1.08);
    opacity: 1;
}

.row_posterLarge {
    max-height: 250px;
}

.closeButton {
    background-color: white;
    color: black;
    border-radius: 20px;
    font-size: 1rem;
    font-weight: 700;
    border: none;
    padding: 8px 12px;
    cursor: pointer;
}

.modal_poster {
    max-height: 180px;
    object-fit: contain;
    margin-right: 20px;
    width: 100%;
}

