.nav {
    position: fixed;
    top: 0;
    padding: 10px;
    width: 100%;
    height: 30px;
    z-index: 1;

    transition-timing-function: ease-in;
    transition: all 0.5s;
}

.nav_contents {
    display: flex;
    justify-content: space-between;
}

.nav_black {
    background-color: #111;
}

.nav_logo {
    position: fixed;
    top: 15px;
    left: 0;
    width: 80px;
    object-fit: contain;
    padding-left: 20px;
    cursor: pointer;
}

.signOut_button {
    position: fixed;
    right: 20px;
    top: 10px;
    padding: 5px 10px;
    font-size: 1rem;
    color: white;
    background-color: #e50914;
    font-weight: 600;
    border: none;
    border-radius: 4.5px;
    cursor: pointer;
    text-decoration: none;
}