.banner {
    position: relative;
    height: 600px;
    color: white;
    object-fit: contain;
}

.banner_contents {
    margin-left: 40px;
    padding-top: 330px;
    height: 190px;
    position: absolute;
    z-index: 2;
}

.banner_title {
    font-size: 3.8rem;
    font-weight: 800;
    padding-bottom: 0.3rem;
    text-shadow: 2px 0px 4px rgba(51, 51, 51, 0.7);
}

.banner_description {
    width: 45rem;
    line-height: 1.3;
    padding-top: 1rem;
    font-size: 0.8rem;
    max-width: 360px;
    height: 80px;
}

.banner_button {
    cursor: pointer;
    color: white;
    outline: none;
    border: none;
    font-weight: 700;
    border-radius: 0.2vw;
    padding: 0.5rem 2rem;
    margin-right: 1rem;
    background-color: rgba(51, 51, 51, 0.7);
}

.banner_button:hover {
    color: #000;
    background-color: #e6e6e6;
    transition: all 0.3s;
}

.banner_fadeBottom {
    height: 13.2rem;
    background-image: linear-gradient(180deg, transparent, rgba(37, 37, 37, 0.61), #111);
    position: relative;
    z-index: 1;
    top: 389px;
}