.landingPage {
    position: relative;
    height: 100%;
    background: url("https://assets.nflxext.com/ffe/siteui/vlv3/f841d4c7-10e1-40af-bcae-07a3f8dc141a/684806ba-f8d5-4005-a73c-3c30975fb763/PH-en-20220502-popsignuptwoweeks-perspective_alpha_website_large.jpg") center no-repeat;
    background-size: cover;
}

.landingPage_gradient {
    width: 100%;
    z-index: 1;
    height: 100vh;
    background: rgba(0, 0, 0, 0.4);
    background-image: linear-gradient(to top, rgba(0, 0, 0, 0.8) 0, rgba(0, 0, 0, 0) 60%, rgba(0, 0, 0, 0.8) 100%);
}

.landingPage_logo {
    position: absolute;
    left: 0;
    width: 150px;
    object-fit: contain;
    padding-left: 20px;
}

.landingPage_button {
    position: absolute;
    right: 20px;
    top: 20px;
    padding: 10px 20px;
    font-size: 1rem;
    color: white;
    background-color: #e50914;
    font-weight: 600;
    border: none;
    border-radius: 4.5px;
    cursor: pointer;
}

.landingPage_body {
    position: absolute;
    top: 30%;
    left: 0;
    right: 0;
    margin-left: auto;
    margin-right: auto;
    text-align: center;
    color: white;
    padding: 20px;
    font-size: 1.5rem;
    z-index: 1;
}

.body_p1 {
    font-size: 1.3rem;
    padding-bottom: 10px;
}

.body_p2 {
    font-size: 1rem;
}

.landingPage_input {
    margin: 20px;
}

.landingPage_input > form > input {
    padding: 10px;
    outline-width: 0;
    height: 30px;
    width: 30%;
    border: none;
    max-width: 600px;
}

.landingPage_getStarted {
    padding: 16px 20px;
    font-size: 1rem;
    color: white;
    background-color: #e50914;
    border: none;
    font-weight: 600;
    cursor: pointer;
}

.section {
    color: white;
    text-align: center;
    background-color: black;
    font-size: 1.5rem;
}

.HR {
    background-color: #111;
    height: 10px;
}

.section_text {
    padding-top: 50px;
    padding-bottom: 10px;
}

.vid_container {
    max-width: 1100px;
    margin: 0 auto;
    max-height: 70vh;
}

.vid_img {
    position: relative;
    margin: auto;
    z-index: 2;
}

.vid_vid {
    z-index: 1;
    position: relative;
    bottom: 400px;
    display: inline;
}

.dl_container {
    margin: 0 auto;
}

.dl_block {
    display: flex;
    justify-content: space-between;
    align-items: center;
    border: 4px solid #111;
    border-radius: 20px;
    background-color: black;
    width: 400px;
    height: 80px;
    z-index: 2;
    position: relative;
    bottom: 130px;
    margin-left: auto;
    margin-right: auto;
}

.dl_card {
    height: 70px;
    margin-left: 20px;
}

.dl_text {
    font-size: 1.3rem;
}

.dl_icon {
    height: 70px;
    margin-right: 10px;
}

.padding {
    padding-bottom: 60px;
}